/* *------------------------Head------------- */
.head{
    background-color: rgb(0,173,96);
    padding: 10px 0;
    color: whitesmoke;
}
.head i {
    margin-right: 10px;
}
.head label{
    margin-right: 30px;
    font-size: 13px;
}
/* *------------------------Head------------- */

/* *------------------------Search------------- */
.search{
    padding: 20px 0;
    
}
.search .logo img{
    width: auto;
    height: 70px;
}
.search .width{
    width: 20%;
}
.searchBox{
    width: 80%;
    border: 2px solid rgba(0,0,0,0.1);
    border-radius: 50px;
}
.searchBox i {
    width: 5%;
    text-align: center;
    font-size: 20px;
    opacity: 0.5;
    padding: 15px 20px;
    font-size: 17px;
}
.searchBox input{
    width: 100%;
    padding: 10px;
    border-radius: 5px;
}
::placeholder{
    font-size: 16px;
}
.searchBox span{
    width: 20%;
    opacity: 0.8;
    border-left: 2px solid rgba(0,0,0,0.1);
    padding: 10px;
}
.cart{
    position: relative;
}
.search .cart span{
    position: absolute;
    top:-1px;
    right: -8px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    text-align: center;
    background-color: #e94560;
    font-size: 12px;
    color: white;
}
.search .f_flex{
    justify-content: end;

}
.search.active {
  height: 12vh;
  position: fixed;
  top: 0;
  z-index: 1000;
  width: 100%;
  background-color: #fff;
  box-shadow: -1px 11px 24px -11px rgba(0, 0, 0, 0.2);
  transition: 0.5s;
}


/* *------------------------Search------------- */
.categrories{
    background-color:#f6f9fc ;
    padding: 0 30px;
    border-radius: 5px;
    height: 4vh;
    margin-top: 5px;
    
}
.categrories span{
    margin-right: 20px;
    font-size: 30px;
    margin-top: 10px;
}
.categrories h4{
    font-weight: 500;
    margin-top: 12px;
}
.categrories h4 i{
    margin-left: 10px;
}
header{
    height: 6vh;
    box-shadow: -1px 15px 17px -8px rgba(0,0,0,0.1);
}
header ul li{
    margin-left: 30px;
}
header ul li a{
    transition: 0.5s;
    font-size: 15px;
    font-weight: 400;
}
header ul li a:hover{
    color: red
}
.open{
    display: none;
}
.search .active{
    height: 12vh;
    position: fixed;
    top:0;
    z-index: 0;
    width: 100%;
    background-color: #fff;
    box-shadow: -1px 11px 24px -11px rgba(0,0,0,0.2);
    transition: 0.5s;
}


@media (max-width: 768px) {
    header ul {
        padding-top: 30px;
    }
    .nav-links-MobileMenu{
        position: absolute;
        display: block;
        list-style-type: none;
        left: 0;
        top: 0;
        transition: 0.5s;
        width: 60%;
        height: 100vh;
        background-color: #fff;
        z-index: 888;
        box-shadow: rgba(50,50,93,0.23) 0 50px 100px -20px, rgba(0,0,0,0.3) 0 30px 60px -30px;
    }
    header ul li{
        width: 100%;
        transition: all 0.5s ease-in-out;
        text-transform: uppercase;
        line-height: 70px;
    }
    header .link{
        display: none;
    }
    .close, 
    .open{
        position: absolute;
        top:35px;
        right: 20px;
        color: #ff014f;
        display: block;
        font-size: 25px;
        z-index: 999;
    }
    .open{
        top: 21%;
        right: 30px;
        background: #e94560;
        color: #fff;
    }
    .close{
        right: 45%;
    }
    .close .home-btn{
        background-color: linear-gradient(145deg, #e2e8ec,#ffffff);
        color:#ff014f ;
        border-radius: 50%;
        padding: 0;
        width: 60%;
        height: 60%;
        line-height: 60px;
    }
}
